.song-container {
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    img {
        width: 20%;
        border-radius: 50%;
    }
    h2 {
        padding: 3rem 1rem 1rem 1rem;
    }
    h3 {
        size: 1rem;
    }
}

@media screen and (max-width: 768px) {
    .song-container{
        img {
            width: 60%
        }
    }
}
    
@media screen and (max-width: 768px) {
    .time-control{
       width: 90%
        }
    .play-control{
        width: 60%;
    }
    }
